code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: black;
  background-image: url(/static/media/pramod-tiwari-3UgbE1b43j8-unsplash.7bd4b931.jpg);
  background-attachment: fixed;
  background-size: 120%;
  margin: 0;
  /* font-family: 'Lilita One', cursive; */
  font-family: 'Fredoka One', cursive;
  color: white
}



.hidden {
  opacity: 0
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.shake-onclick:active {
  -webkit-animation: tilt-n-move-shaking .6s;
          animation: tilt-n-move-shaking .6s
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes tilt-n-move-shaking {
  0% {
    -webkit-transform: translate(0, 0) rotate(0deg);
            transform: translate(0, 0) rotate(0deg);
  }

  25% {
    -webkit-transform: translate(5px, 5px) rotate(5deg);
            transform: translate(5px, 5px) rotate(5deg);
  }

  50% {
    -webkit-transform: translate(0, 0) rotate(0eg);
            transform: translate(0, 0) rotate(0eg);
  }

  75% {
    -webkit-transform: translate(-5px, 5px) rotate(-5deg);
            transform: translate(-5px, 5px) rotate(-5deg);
  }

  100% {
    -webkit-transform: translate(0, 0) rotate(0deg);
            transform: translate(0, 0) rotate(0deg);
  }
}

@keyframes tilt-n-move-shaking {
  0% {
    -webkit-transform: translate(0, 0) rotate(0deg);
            transform: translate(0, 0) rotate(0deg);
  }

  25% {
    -webkit-transform: translate(5px, 5px) rotate(5deg);
            transform: translate(5px, 5px) rotate(5deg);
  }

  50% {
    -webkit-transform: translate(0, 0) rotate(0eg);
            transform: translate(0, 0) rotate(0eg);
  }

  75% {
    -webkit-transform: translate(-5px, 5px) rotate(-5deg);
            transform: translate(-5px, 5px) rotate(-5deg);
  }

  100% {
    -webkit-transform: translate(0, 0) rotate(0deg);
            transform: translate(0, 0) rotate(0deg);
  }
}
.dice {
    height: 150px;
    width: 150px;
    position: relative;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    transition: -webkit-transform .96s;
    transition: transform .96s;
    transition: transform .96s, -webkit-transform .96s;
    background-color: rgb(198, 198, 198);
}

.panel {
    border-radius: 10px;
    width: 150px;
    height: 150px;
    -webkit-perspective: 400px;
            perspective: 400px;
    position: relative;
    left: 50%;
    top: 30%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.dot {
    display: block;
    width: 28px;
    height: 28px;
    border-radius: 50%;
    margin: 8px;
    background-color: Black;
}

.side {
    position: absolute;
    background-color: rgb(231, 218, 218);
    width: 150px;
    height: 150px;
    border-radius: 16px;
    line-height: 150px;
    border-color: rgb(72, 71, 71);
    border-width: 5px;
    border-style: ridge;

}

.one {
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transform: rotateX(-90deg) translateZ(74px);
            transform: rotateX(-90deg) translateZ(74px);
    z-index: 1;
}

.two {
    display: flex;
    justify-content: space-between;
    -webkit-transform: rotateY(180deg) translateZ(74px);
            transform: rotateY(180deg) translateZ(74px);
    z-index: 2;
}

.two .dot:nth-of-type(2) {
    align-self: flex-end;
}

.three {
    display: flex;
    justify-content: space-between;
    -webkit-transform: rotateY(-90deg) translateZ(74px);
            transform: rotateY(-90deg) translateZ(74px);
    z-index: 3;
}

.three .dot:nth-of-type(2) {
    align-self: center;
}

.three .dot:nth-of-type(3) {
    align-self: flex-end;
}

.four {
    z-index: 4;
    -webkit-transform: rotateY(0deg) translateZ(74px);
            transform: rotateY(0deg) translateZ(74px);
}

.four,
.six {
    display: flex;
    justify-content: space-between;
}

.four .kolona,
.six .kolona {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.five {
    display: flex;
    justify-content: space-between;
    -webkit-transform: rotateY(90deg) translateZ(74px);
            transform: rotateY(90deg) translateZ(74px);
    z-index: 5;
}

.five .kolona {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.five .kolona:nth-of-type(2) {
    justify-content: center;
}

.six {
    -webkit-transform: rotateX(90deg) translateZ(74px);
            transform: rotateX(90deg) translateZ(74px);
    z-index: 6;
}

.diceBtn {
    background-color: rgb(24, 137, 218);
    padding: .8em;
    margin: .6em;
    font-size: x-large;
}
